@import '../assets/style/_mixins.scss';
.contacts__header {
  display: flex;
  width: fit-content;
  font-weight: 400;
  color: var(--body-color);
  margin: 30px auto;
  text-align: center;
}

.contacts__subheader {
  display: flex;
  width: fit-content;
  font-weight: 400;
  color: var(--body-color);
  margin: 30px auto;
  text-align: center;

  span {
    color: var(--highlight-color);
    margin-right: 10px;
  }
}